.app {
  width: 800px;
  max-width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.success {
  background-color: rgb(166, 245, 166);
  border-radius: 5px;
  width: 400px;
  padding: 10px 0;
  margin-bottom: 25px;
}

.success p {
  color: green;
  font-weight: 700;
  font-size: 18px;
  margin: 0;
  text-align: center;
}

.error {
  background-color: rgb(245, 166, 166);
  border-radius: 5px;
  width: 400px;
  padding: 10px 0;
  margin-bottom: 25px;
}

.error p {
  color: red;
  font-weight: 700;
  font-size: 18px;
  margin: 0;
  text-align: center;
}